<template>
    <div v-for="article in articles" :key="article.title">
        <img :src="article.urlToImage"/>
        <h1 class="headline">{{ article.title }}</h1>
        <p>
           {{ article.description }}
        </p>
        <span>
            {{article.source.name}}
        </span>
        <a :href="article.url" target="_blank" >Read More</a>
    </div>
</template>

<script>
    export default {
        props: {
            articles: Array
        }
    }
</script>